import React, {Component} from 'react';
import {connect} from 'react-redux';
// import {NavLink} from 'react-router-dom';
import {Row, Col, Image, Card} from 'react-bootstrap';
import {getMoveDetails,getMoveVideo} from "../../actions";

class MovieSingle extends Component{

	// React-Thunk
	fetchMovieDetails() {
		return (dispatch) => {
			const {move_single} = this.props;
			const url = 'https://api.themoviedb.org/3/movie/'+move_single+'?api_key=273066d34680eff10763b530228eb3df&language=en-US';

			fetch(url)
				.then(res => res.json())
				.then(
					data => dispatch( getMoveDetails(data) )
				)
				.catch(function(error) {
					console.log('ОШИБКА загрузки ' + error);
				});
		}
	};

	fetchMovieVideo() {
		return (dispatch) => {
			const {move_single} = this.props;
			const url = 'https://api.themoviedb.org/3/movie/'+move_single+'/videos?api_key=273066d34680eff10763b530228eb3df&language=en-US';

			fetch(url)
				.then(res => res.json())
				.then(
					data => dispatch( getMoveVideo(data) )
				)
				.catch(function(error) {
					console.log('ОШИБКА загрузки ' + error);
				});
		}
	};


	componentDidMount(){
		this.props.dispatch( this.fetchMovieDetails() );
		this.props.dispatch( this.fetchMovieVideo() );
	}

	render(){
		const {move_details} = this.props;
		const {results} = this.props.move_video;
		const {genres,production_countries, production_companies} = this.props.move_details;
		return(
			<React.Fragment>
				<h1 className="py-5">Фильм</h1>
				<Row>
					<Col sm lg="5">
						<h2>{move_details.original_title}</h2>
						<Image style={{maxWidth: "100%"}} src=
							{
					          (move_details.poster_path) ?
						          'https://image.tmdb.org/t/p/w500' + move_details.poster_path :
						          'http://lascrucesfilmfest.com/wp-content/uploads/2018/01/no-poster-available-737x1024.jpg'
				            }
						 rounded />
					</Col>
					<Col sm lg="7" className="pt-5">
						<Card>
							<Card.Body>
								<p><b>Дата премьеры: </b> {move_details.release_date}</p>
								<p><b>Страна: </b>
									{ production_countries ? production_countries.map(item => item.name + " | ") : null }
									</p>
								<p><b>Жанр: </b>
									{ genres ? genres.map(item => item.name + " | ") : null }
								</p>
								<p><b>Студия: </b>
									{ production_companies ? production_companies.map(item => item.name + " | ") : null }
								</p>
								<Card.Subtitle className="mb-2 text-muted">
									<b>Слоган: </b>
									{move_details.tagline}
									</Card.Subtitle>
								<Card.Text>
									<b>Описание: </b>
									{move_details.overview}
								</Card.Text>
								<small><b>Популярность:</b> {move_details.popularity}</small>
								<p><b>Рейтинг:</b> {move_details.vote_average}</p>

								{
									results ? results.map( item =>

										<div className="video my-4" key={item.id}>
											<p>{item.name}</p>
											<iframe width="100%" height="340" src={"https://www.youtube.com/embed/" + item.key}
											        frameBorder="0"
											        allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
											        allowFullScreen>
											</iframe>
										</div>

									) : null
								}
							</Card.Body>
						</Card>
					</Col>
				</Row>
			</React.Fragment>
		);
	}
}

const mapStateToProps = (state) => {
	return{
		posts: state.posts,
		move_single: state.move_single,
		move_details: state.move_details,
		move_video: state.move_video,
	}
};

export default connect(mapStateToProps)(MovieSingle);