import {SEARCH_MOVE} from '../actions/index.jsx';

const SearchReducer = (state = [], action) => {
  switch(action.type){
	  case SEARCH_MOVE:
	    return action.data;
	  // case SEARCH_MOVE_RESULT:
		//   return {state,...action.data};
    default:
      return state;
  }
};

export default SearchReducer;
