import {MOVIE_RATING,UPDATE_MOVIE_RATING} from '../actions/index.jsx';

const PostsReducer = (state = [], action) => {
  switch(action.type){
	  case MOVIE_RATING:
	    return action.data;
	  case UPDATE_MOVIE_RATING:
		  return state.concat(action.data);
    default:
      return state;
  }
};

export default PostsReducer;
