
const MOVIE_RATING = 'MOVIE_RATING';
const SEARCH_MOVE = 'SEARCH_MOVE';
const SEARCH_MOVE_RESULT = 'SEARCH_MOVE_RESULT';
const MOVIE_ID = 'MOVIE_ID';
const MOVIE_DETAILS = 'MOVIE_DETAILS';
const MOVIE_VIDEO = 'MOVIE_VIDEO';
const UPDATE_MOVIE_RATING = 'UPDATE_MOVIE_RATING';

const getMovieRating = (data) => {
	return{
		type: MOVIE_RATING,
		data: data
	}
};

const getSearchInput = (data) => {
	return{
		type: SEARCH_MOVE,
		data: data
	}
};

const getSearchMoveResult = data => {
	return{
		type: SEARCH_MOVE_RESULT,
		data: data
	}
};

const getMoveID = data => {
	return{
		type: MOVIE_ID,
		data: data
	}
};

const getMoveDetails = data => {
	return{
		type: MOVIE_DETAILS,
		data: data
	}
};

const getMoveVideo = data => {
	return{
		type: MOVIE_VIDEO,
		data: data
	}
};

const getUpdateMovieRating = data => {
	return{
		type: UPDATE_MOVIE_RATING,
		data: data
	}
};


export {
	MOVIE_RATING,
	SEARCH_MOVE,
	SEARCH_MOVE_RESULT,
	MOVIE_ID,
	MOVIE_DETAILS,
	MOVIE_VIDEO,
	UPDATE_MOVIE_RATING,
	getMovieRating,
	getSearchInput,
	getSearchMoveResult,
	getMoveID,
	getMoveDetails,
	getMoveVideo,
	getUpdateMovieRating
};