import React, {Component} from 'react';
import {connect} from 'react-redux';
import {Form,FormControl} from 'react-bootstrap';
import {NavLink} from 'react-router-dom';

import {getSearchInput} from '../../actions/index.jsx';

class SearchInput extends Component {


	fetchSearchResult(){
		return (dispatch) => {
			// let data = {};
			const value = this.searchInput.value;
			const url = 'https://api.themoviedb.org/3/search/movie?api_key=273066d34680eff10763b530228eb3df&language=en-US&query='+ value  +'&page=1&include_adult=false';

			fetch(url)
				.then(res => res.json())
				.then(
					data => {
						dispatch( getSearchInput(data) )
					}
				)
				.catch(function(error) {
					console.log('ОШИБКА загрузки ' + error);
				});
		}
	}

	// React-Thunk
	handleSearchMove(){
		this.props.dispatch( this.fetchSearchResult() );
	}

	// handleKeyPress (event){
	// 	console.log(event.keyCode);
	// 	if (event.keyCode === 13) {
	// 		// event.preventDefault();
	// 		return this.handleSearchMove();
	// 	}
	// }

	render(){
		return(
			<React.Fragment>
				<Form inline>
					<FormControl type="text"
			             placeholder="Search"
			             className="mr-sm-2"
			             ref={input => {this.searchInput = input}}
			             // onKeyDown={this.handleKeyPress.bind(this)}
					/>
					<NavLink to="/search"
					    className="btn btn-info mx-0"
						onClick={this.handleSearchMove.bind(this)}
					>Search</NavLink>
				</Form>
			</React.Fragment>
		);
	}
}

const mapStateToProps = (state) => {
	return state;
};

export default connect(mapStateToProps)(SearchInput);