import React, {Component} from 'react';
import {connect} from 'react-redux';
import {NavLink} from 'react-router-dom';
import {Card, CardColumns} from 'react-bootstrap';
import {getMoveID} from "../../actions";

class SearchView extends Component{

	handleGetMovieID(id){
		this.props.dispatch(getMoveID(id));
	}

	render(){
		const {results} = this.props.search_move;

		return(
			<React.Fragment>
				<h1 className="py-5">Результат Поиска</h1>
				<CardColumns className="card-columns_col">
					{
						results ? results.map(post =>

								<Card key={post.id}>
									<NavLink
										exact to={"/movie/"+post.id}
										className="nav-link m-0 p-0"
										onClick={this.handleGetMovieID.bind(this,post.id)}>
										<Card.Img variant="top" src=
											{
												(post.poster_path) ?
													'https://image.tmdb.org/t/p/w500' + post.poster_path :
													'http://lascrucesfilmfest.com/wp-content/uploads/2018/01/no-poster-available-737x1024.jpg'
											}
										/>
										<Card.Body>
											<Card.Title>{post.title}</Card.Title>
											<Card.Text>
												{post.overview}
											</Card.Text>
											<small> <b>Популярность: {post.popularity}</b> </small>
											<p><b>Рейтинг:</b> {post.vote_average}</p>
										</Card.Body>
									</NavLink>
								</Card>

							) :
							null
					}
				</CardColumns>
			</React.Fragment>
		);
	}
}

const mapStateToProps = (state) => {
	return{
		posts: state.posts,
		search_move: state.search_move
	}
};

export default connect(mapStateToProps)(SearchView);