import React, {Component} from 'react';
import {Route, Switch, BrowserRouter} from 'react-router-dom';
import {Container} from 'react-bootstrap';

import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import './App.css';

import Header from './Components/Header/index.jsx';
import Home from './Components/Home/index.jsx';
import MovieRating from './Components/MovieRating/index.jsx';
import SearchView from './Components/SearchView/index.jsx';
import MovieSingle from './Components/MovieSingle/index.jsx';

class App extends Component{

	render(){
		return(
			<BrowserRouter>
				<React.Fragment>
					<header>
						<Header/>
					</header>
					<main>
						<Container>
							<Switch>
								<Route exact path='/' component={Home}/>
								<Route exact path='/movie_rating' component={MovieRating}/>
								<Route path='/search' component={SearchView}/>
								<Route path='/movie/:id' component={MovieSingle}/>
							</Switch>
						</Container>
					</main>
				</React.Fragment>
			</BrowserRouter>
		);
	}
}

export default App;
